	module.exports = {"data":{"page":{"id":"/Users/zwhitten/Projects/zw_blog/content/pages/1--about/index.md absPath of file >>> MarkdownRemark","html":"<p>Hey thanks for checking out my page! My name is Zach. I’ve been a Software Engineer for over a decade now.\nMost of my career I’ve spent in the Java world. Everything from desktop Swing applications to web services handling millions of requests a day. </p>\n<p>In my spare time I jump into whatever languages or frameworks have caught my interest at the time. I’m currently on an Elixir/Phoenix kick with some Rust/Clojure/Go on the side. Next week/month I may be on to something else.</p>\n<p>My posts here may span a range of topics, probably things I’m not an expert in because that’s more fun for me. If you find any mistakes or have any suggestions (or better yet if you like something I’ve posted) feel free to drop me a line on Twitter, leave a comment, or send a pull request on github. </p>\n<p><a href=\"https://twitter.com/zwhitten\">@zwhitten</a></p>","frontmatter":{"title":"About"}},"footnote":{"id":"/Users/zwhitten/Projects/zw_blog/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>this site is the personal blog of <a href=\"https://zdub.io\">@zwhitten</a></li>\n<li>built using <a href=\"https://github.com/gatsbyjs/gatsby\">Gatsby</a></li>\n<li>hosted on <a href=\"https://github.com/zwhitten/zwhitten.github.io\">Github</a></li>\n<li>photos by <a href=\"https://unsplash.com\">unsplash.com</a></li>\n</ul>"},"site":{"siteMetadata":{"title":"Codes of Electric Sheep"}}},"pathContext":{"slug":"/about/"}}

